//Base
@import url('https://fonts.googleapis.com/css2?family=Glory:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800&family=Inter:wght@100;200;500;600;700&family=Noto+Sans+Georgian:wght@300;400;500;600;800&family=Nunito:wght@200&display=swap');
$black: #000;
$bgnavtop: #fdc800;
$bgnavbar: #f0f4f9;
$navlogo: #002147;
$btnYellow: #fdc800;
$whitebtn: #ffffff;
$cardfont: #757575;

* {
    font-family: "Inter", sans-serif;
}
.header {
    // top navbar
    .navbar-top {
        background-color: $bgnavtop;
        padding-block: 12px;
        .contact-info {
            li {
                a {
                    font-size: 14px;
                    color: $black;
                    i {
                        padding-right: 10px;
                    }
                }

                &:not(:last-child) {
                    @media screen and (min-width: 768px) {
                        margin-right: 54px;
                    }
                }
            }
        }
        .social-icons {
            li {
                a {
                    font-size: 13px;
                    color: $black;
                }
                &:not(:last-child) {
                    margin-right: 24px;
                }
            }
        }
    }
    // End Navbar

    // start
    .navbar {
        // top: 0;
        // position: -webkit-sticky;
        // position: sticky;
        background-color: #f0f4f9;
        height: 100px;
        .navbar-brand {
            color: $navlogo;
        }
        .navbar-nav {
            padding-right: 100px;
            .nav-item {
                .nav-link {
                    color: $navlogo;
                    font-weight: bold;
                    padding-left: 20px;
                }
            }
        }
        .search {
            margin-right: 134px;
            .btn1 {
                background-color: $btnYellow; /* Green */
                border: none;
                color: $black;
                text-align: center;
                text-decoration: none;
                display: inline-block;
                font-size: 16px;
                margin-right: 20px;
                border-radius: 4px;

                width: 100px;

                cursor: pointer;
            }
            // .sign-input{
            //     width: 44px;
            //     height: 44px;
            //     background-color: white;
            //     border: 1px solid  #BDBDBD;
            //     border-radius: 6px;
            //     display: flex;
            //     align-items: center;

            // }
            // i{
            //     font-size: 20px;
            //     color: $black;
            //     flex: 1;
            // }

            .has-search .form-control {
                padding-left: 2.375rem;
                width: 44px;
                height: 44px;
            }

            .has-search .form-control-feedback {
                position: absolute;
                z-index: 2;
                display: block;
                width: 2.375rem;
                height: 2.375rem;
                line-height: 2.375rem;
                text-align: center;
                pointer-events: none;
                color: $black;
            }
        }
    }
}
.content-wrapper {
    .it-wreapper {
        padding-bottom: 87px;
        background-color: $bgnavbar;
        .image {
            margin: 30px 40px 60px 10px;
        }
        .text {
            margin: 115px 0px 160px 10px;
            h4 {
                font-family: "Times New Roman", Times, serif;
                font-weight: bold;
                color: $navlogo;
            }
            h1 {
                font-family: Georgia, "Times New Roman", Times, serif;
                font-weight: 60;
                color: $navlogo;
            }
            .multi-btns {
                .btn {
                    background-color: $btnYellow; /* Green */
                    border: none;
                    color: $black;
                    height: 40px;
                    padding-top: 10px;
                    font-size: 13px;
                    margin-right: 10px;
                    border-radius: 4px;

                    width: 100px;

                    cursor: pointer;
                }
                .btn2 {
                    background-color: $whitebtn; /* Green */
                    border: none;
                    color: $black;

                    font-size: 12px;
                    margin-right: 10px;
                    border-radius: 4px;

                    width: 100px;

                    cursor: pointer;
                }
            }
        }
    }
    .timing-wrapper {
     
        .timing-inner-wrapper{
            position: relative;
        margin-top: -87px;
        }
        .container {
            background-color: $navlogo;
            color: $whitebtn;
            .image-text {
                .image {
                    padding-left: 30px;
                }
                .text-up {
                    font-family: Georgia, "Times New Roman", Times, serif;
                    font-weight: bold;
                    margin-left: 15px;
                    .change-text-c {
                        color: $btnYellow;
                    }
                }
            }
            .lanch-time {
                .time {
                    flex-basis: 100px;
                    margin-left: 30px;

                    .t-hours {
                        margin-top: 50px;
                        font-size: 50px;
                        margin-bottom: -12px;
                        font-family: "Glory", sans-serif;
                        // font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
                        //font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
                        font-weight: 1000;
                        .time-dot {
                            padding-left: 25px;
                            font-size: 30px;

                            font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode",
                                Geneva, Verdana, sans-serif;
                        }
                    }
                    .t-minutes {
                        margin-left: 5px;
                    }
                }
            }
        }
    }

    .top-feature-wrappers {
        .top-heading {
            color: $navlogo;
            font-weight: bold;
            font-family: Georgia, 'Times New Roman', Times, serif;
        }
        .card-image {
            position: relative;

            .card-short-image {
                position: absolute;
                top: 72%;
                left: 10px;
                border: 7px solid $whitebtn;
            }
        }
        .card-heading {
            padding-top: 15px;
            font-weight: bold;
            font-family: "Times New Roman", Times, serif;
            font-size: 15px;
            color: $navlogo;
        }
        .card-text {
            font-family: Georgia, "Times New Roman", Times, serif;
            color: $cardfont;
            font-size: 14px;
            margin-top: 20px;
        }
    }
    // subscribe-------------------------------------------------------------------------------------
    .suscribe-wrapper {
        .subscribe-image {
            margin-left: 90px;
            
        }


        .sus-content {
            margin-top: 180px;
            .form-input{
                position: relative;
                .btn-umair{
                    position: absolute;
                    background-color: $bgnavtop;
                    margin-left: 313px;
                    width: 164px;
                    border-radius: 5px;
                    margin-top: 2px;
                    border: 1px solid $whitebtn;
                    color: #002147;
                    padding-top: 5px;
                    
                }
                .icon {
                    
                    position: absolute;
                    left: 15px;
                    top: 0;
                    font-size: 25px;
                    color:$black;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
                input {
                    border: 1px solid #BDBDBD;
                    border-radius: 5px;
                    padding: 5px 10px 5px 50px;
                    font-size: 18px;
                    line-height: 30px;
                    outline: 0;
                    display: block;
                    width: 88%;
                }
            }
            .subs-heading {
                font-size: 16px;
                color: $bgnavtop;
                font-weight: bold;
            }
        }
        .main-heading {
            font-family:Georgia, 'Times New Roman', Times, serif;
            font-size: 48px;
            color: $navlogo;
        }
    }

    .result-rapper{
        background-color: #F0F4F9;
       .container{
        background: url("/assets/images/map.png");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        .result-seciton{
            
        .result-image{
            margin-top: 30px;
            margin-bottom: 30px;
        }
        .result-para{
          
        .result-number{
            margin-right: -40px;
            margin-top: -50px;
            color: $navlogo;
            font-family: 'Noto Sans Georgian', sans-serif;
            font-size: 60px;
            font-weight: bold;

        }
        .result-alph{
            margin-top: -30px;
            font-size: 16px;
            margin-bottom: 40px;
            color: #00B2B2;
            font-weight: bold;
            font-family: 'Noto Sans Georgian', sans-serif;
            margin-left: 40px;
        }
    }
    }
        .result-heading{
            
        }
       }
    }


    .feedback-wrapper{
        .feeback-row{
        .feedback-main-image{
             margin-left: 30px;
             margin-top: 30px;
             margin-bottom: 30px;
            .feed-upper-image{
              margin-top:30px;
              margin-left: 30px;
              background-color: $btnYellow;
              border-radius: 3px;
              padding: 10px 10px 10px 10px;
            }
            .feedback-content{
                background-color: white;
                width: 400px;
                border-radius: 3px;
                .feed-heading{
                     color: #212529;
                     font-size: 20px;
                     padding-top: 25px;
                     font-family:Georgia, 'Times New Roman', Times, serif;
                    
                }
                .feed-sub-heading{
                    color: #757575;
                    font-size: 14px;
                    margin-top: -15px;
                }
 
            }
        }
    }
    }
}
