@media screen and (max-width: 667px) {
    .image {
        width: 300px;
        margin: 0;
        padding-bottom: 100px;
    }
    .copyright {
        font-size: 9px;
    }
}
@media screen and (max-width: 992px) {
    .lanch-time {
        font-size: 5px;
        margin-left: -15px;
    }
}

@media screen and(min-width:768px) and (max-width:1199px){
